import "./style/header.css"
import bg from "../../pic/logoBg.png"
import logo from "../../pic/logoTextSqu.png"

function Header () {
    return(
        <div className="header">
            <img className="header__pic" src={bg} alt=""/>
            <img className="header__title" src={logo} alt=""/>
        </div>
    )
}

export default Header